import { Form } from '@/components/ui/form';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useMemo, useState } from 'react';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { toast } from 'sonner';
import TextInputForm from '@/components/forms/TextInputForm';
import TextAreaInputForm from '@/components/forms/TextAreaInputForm';
import { useNavigate } from 'react-router-dom';
import SelectInputForm from '@/components/forms/SelectInputForm';
import TimeInputForm from '@/components/forms/TimeInputForm';
import { DateInputForm } from '@/components/forms/DateInputForm';
import FileInput from './FileInput';
import { RequestPreviewFormValues, RequestPreviewSchema } from './schemas/request-preview-schema';
import { ExtraService } from '@/features/selfServices/types/extraServices.types';
import { formatDate } from 'date-fns';
import { SubtleButton } from '@/components/buttons/SubtleButton';

interface Props {
    request: ExtraService;
}

export default function RequestPreviewForm({ request }: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();

    const defaultValues = useMemo(
        () => ({
            employeeName: request.employee.name || '',
            service_type: request.service_type || '',
            purpose: request.purpose || '',
            time: request.time || '',
            date: new Date(request.date) || '',
            note: request.note || '',
            attachment: request.attachment || '',
        }),
        [request],
    );

    const form = useForm<RequestPreviewFormValues>({
        resolver: zodResolver(RequestPreviewSchema),
        defaultValues,
    });

    const onSubmit = async (data: RequestPreviewFormValues) => {
        setLoading(true);
        setError(null); // Clear previous errors

        try {
            await apiPost({
                endpoint: endpoints.extraServices.update + request.id,
                body: { ...data, date: formatDate(data.date, 'yyyy-MM-dd'), status: 'approved' },
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });

            toast('تمت الموافقة بنجاح');
            navigate('/human-resources/self-services');
        } catch (error: any) {
            if (error.response?.data?.errors) {
                // Handle field-specific errors
                Object.entries(error.response.data.errors).forEach(([field, messages]) => {
                    form.setError(field as any, {
                        type: 'manual',
                        message: (messages as string[]).join(', '),
                    });
                });
            } else {
                // Handle generic error
                let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
                if (error.response) {
                    errorMessage = error.response.data.msg || errorMessage;
                } else if (error.request) {
                    errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
                }
                setError(errorMessage);
            }
        } finally {
            setLoading(false);
        }
    };

    const rejectRequest = async () => {
        setLoading(true);
        setError(null); // Clear previous errors

        try {
            await apiPost({
                endpoint: endpoints.extraServices.update + request.id,
                body: {
                    status: 'rejected',
                    reject_reason: form.getValues('reject_reason'),
                    reject_attachment: form.getValues('reject_attachment'),
                },
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });

            toast('تمت رفض الطلب بنجاح');
            navigate('/human-resources/self-services');
        } catch (error: any) {
            // Handle generic error
            let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
            if (error.response) {
                errorMessage = error.response.data.msg || errorMessage;
            } else if (error.request) {
                errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
            }
            setError(errorMessage);
        } finally {
            setLoading(false);
        }
    };

    return (
        <>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="mt-6">
                    {error && (
                        <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                            <div className="flex justify-between items-center">
                                <span className="">{error}</span>
                                <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                    ×
                                </button>
                            </div>
                        </div>
                    )}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-x-6">
                        <TextInputForm disabled name="employeeName" label="اسم الموظف" placeholder="" />
                        <SelectInputForm
                            name="service_type"
                            label="نوع الخدمة"
                            placeholder="طلب تقرير"
                            disabled
                            options={[
                                { name: 'شهادة خبرة', value: 'experience certificate' },
                                { name: 'طلب تدريب', value: 'trainee' },
                                { name: 'حجز تذكرة طلب اجتماع', value: 'meeting ticket' },
                                { name: 'استقالة', value: 'resignation' },
                                { name: 'طلب تقرير', value: 'report request' },
                            ]}
                        />
                        <TextInputForm disabled name="purpose" label="الغرض" placeholder="متابعة الاداء" />
                        <TimeInputForm disabled={true} name="time" label="الوقت" placeholder="9:00 PM" />
                        <DateInputForm disabledDate name="date" label="التاريخ" placeholder="2025/05/10" />
                    </div>
                    <TextAreaInputForm
                        disabled
                        className="flex flex-col min-h-[130px] mb-1"
                        name="note"
                        label="ملاحظات اضافية"
                        placeholder="اكتب ملاحظات اضافية"
                    />
                    {/* <FileInput name="attachment" label="رفع ملف" multiple={false} maxSize={10 * 1024 * 1024} /> */}

                    <TextAreaInputForm
                        disabled
                        className="flex flex-col min-h-[130px] mb-1"
                        name="reject_reason"
                        label="ملاحظات منشئ الطلب"
                        placeholder="اكتب ملاحظات اضافية"
                    />
                    <FileInput name="reject_attachment" label="رفع ملف" multiple={false} maxSize={10 * 1024 * 1024} />

                    <div className="flex flex-row justify-end gap-4 mt-12 mb-14">
                        <SubtleButton
                            type="button"
                            className="text-base bg-red-error-50 !px-16 text-[#A21515] leading-5 hover:bg-red-error-50 hover:opacity-80 tracking-normal py-[10px] font-medium"
                            onClick={rejectRequest}
                        >
                            رفض
                        </SubtleButton>
                        <PrimaryButton
                            type="submit"
                            className="text-base leading-5 !px-28 tracking-normal font-medium py-[10px]"
                        >
                            {loading ? 'جاري الموافقة...' : 'موافقة'}
                        </PrimaryButton>
                    </div>
                </form>
            </Form>
        </>
    );
}
