import { SubtleButton } from '@/components/buttons/SubtleButton';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { Link, useParams } from 'react-router-dom';
import RequestPreviewForm from '../forms/RequestPreviewForm';
import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { ExtraService } from '@/features/selfServices/types/extraServices.types';
import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';

export default function RequestPreviewPage() {
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [request, setRequest] = useState<ExtraService | null>(null);

    const params = useParams();
    const { id } = params;

    useEffect(() => {
        const fetchRequest = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.extraServices.show + id,
                });
                setRequest(response.data.data);
            } catch (error) {
                setError('Failed to load request details');
            } finally {
                setLoading(false);
            }
        };
        if (id) {
            fetchRequest();
        } else {
            setError('Invalid request ID');
            setLoading(false);
        }
    }, [id]);

    if (loading) {
        return <LoadingSpinner />;
    }

    if (error) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl text-red-500">{error}</h2>
            </div>
        );
    }

    if (!request) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl">Request not found</h2>
            </div>
        );
    }
    return (
        <div className="w-full mt-6">
            <div className="flex flex-row justify-between mb-6">
                <UnderlineHeading text="معاينة الطلب" />
                <Link to="/human-resources/self-services">
                    <SubtleButton className="bg-white-blue-50 text-base font-medium leading-[125%] text-white-blue-900 border border-white-blue-500 shadow-[0px_1px_2px_0px_#1018280D]">
                        عودة لقائمة الطلبات
                    </SubtleButton>
                </Link>
            </div>
            <RequestPreviewForm request={request} />
        </div>
    );
}
